.header__icon{
    object-fit: contain;
    height: 100px;
    margin-left: 20px;
  
}

.header{
    display: flex;
    align-self: center;
    justify-content: space-between;
    position: sticky;
    top:0;
    background-color: white;
    z-index: 100;
    width: 100%;
}

.header__center{
    display: flex;
    align-self: center;
    flex :1;
    max-width: fit-content;
    padding: 10px;
    height: 30px;
    border: 1px solid lightgray;
    border-radius: 999px;
}


.header__center > input{
    border: none;
    widows: 250px;
    padding: 10px;
    outline-width: 0;
}

.header__right{
    display: flex;
    align-self: center;
    justify-content: space-between;
    width:15vw;
    margin-right: 80px;

}